import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { HostListener } from '@angular/core';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
chats: Array<any> = [];

populate_chat(identity,text){
    this.chats.push({identity: identity,text: text})
    setTimeout(function(){
    var element = document.getElementById("myLabel");
  element.scrollTop = element.scrollHeight - element.clientHeight;
    
    }, 10);
}  

getcontent() {
    if (this.chatdata != null){
        this.populate_chat(1,this.chatdata)
    }
    this.chatdata = null
  }
  
refresh(){
this.chats = [];
}  

eventHandler(keyCode){
    if (keyCode.keyCode == 13){
            this.getcontent();
        }
  }

  constructor(public navCtrl: NavController) {

  this.chatdata = null
  this.chats.push({identity: 0,text: "hello!"},{identity: 1,text: "I am fine!"},{identity: 0,text: "What the fuck are you doing?"})
              }  

}



